import React, { useState } from "react"
import { Link, graphql, useStaticQuery } from "gatsby"
import Img from "gatsby-image"

import Row from "react-bootstrap/Row"
import Col from "react-bootstrap/Col"

import pose, { PoseGroup } from "react-pose"
import { tween } from "popmotion"

import Layout from "../components/layout"
import SEO from "../components/seo"

const imgStyle = {
  marginRight: "auto",
  marginLeft: "auto",
  display: "block",
}

const nameStyle = {
  textAlign: "center",
}

const PoseCol = pose(Col)({
  flip: {
    transition: tween,
  }
})

const Champions = () => {
  const data = useStaticQuery(graphql`
    {
      allChampionsJson(sort: {fields: name}) {
        edges {
          node {
            name
            fields {
              pathName
            }
            id
            stats {
              hp
            }
            thumbnailImg {
              childImageSharp {
                fixed(width: 120, quality: 100) {
                  ...GatsbyImageSharpFixed
                }
              }
            }
          }
        }
      }
    }
  `)

  const sleepMS = (ms) => {
    return new Promise(resolve => setTimeout(resolve, ms));
  }

  const [sortType, setSortType] = useState(true);
  const [toggleButtonText, setToggleButtonText] = useState('Sort by HP');

  const organize = () => {
    const normal = data.allChampionsJson.edges;
    const reversed = normal.slice(0);
    reversed.sort((a, b) => (a.node.stats.hp - b.node.stats.hp));

    const layout = ( obj ) => {
      return(
        <PoseCol xs={6} md={3} lg={2} className="align-content-center" style={{marginBottom: `20px`}} key={obj.node.id}>
          <Link to={obj.node.fields.pathName}>
            <h1 style={nameStyle}>{obj.node.name}</h1>
            <Img 
              style={imgStyle}
              fixed={obj.node.thumbnailImg.childImageSharp.fixed}
              alt={obj.node.name}
            />
          </Link>
        </PoseCol>
      )
    }

    if (sortType) {
      return(
        normal.map(layout)
      )
    } else {
      return(
        reversed.map(layout)
      )
    }

  }

  // const content = data.allChampionsJson.edges.map(( obj ) => (
  //   <Col xs={6} md={4} className="align-content-center">
  //     <Link to={obj.node.fields.pathName}>
  //       <h1 style={nameStyle}>{obj.node.name}</h1>
  //       <img style={imgStyle} src={`http://ddragon.leagueoflegends.com/cdn/9.22.1/img/champion/${obj.node.id}.png`} alt={`${obj.node.name}`}></img>
  //     </Link>
  //   </Col>
  // ))

  const handleButtonClick = async () => {
    // setFading(false);
    // await sleepMS(250);
    if(sortType) {
      setToggleButtonText('Sort by Name');
      setSortType(false);
    } else {
      setToggleButtonText('Sort by HP');
      setSortType(true);
    }
    // await sleepMS(100);
    // setFading(true);
  }

  return (
    <Layout activeIndex='champions'>
      <SEO title={"Champions"}/>
      {/* <h1>Champions Page</h1> */}
      {/* <Button onClick={handleButtonClick}>{toggleButtonText}</Button> */}
      <Row>
      <PoseGroup>
        {organize()}
      </PoseGroup>
      </Row>
    </Layout>
  )
}

export default Champions